.sorting {
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-start;
    
    &.is-selected {
        .sorting__selected-position {
            display: flex;
        }
        .sorting__btn:before {                 
            display: inline-flex; 
        }
    }
    //Помещает позицию в столбец
    &_col {
        .sorting__btn {
            flex-direction: column;
        }
        .sorting__selected-position {
            font-size: 10px;
            color: #606266;
        }
    }

    &__btn {
        display: flex;
        flex-direction: row;
        position: relative;
        &:hover {
            color: $color-primary;
            cursor: pointer;
            &:after {
                background: url("~images/active_custom_card/arrow-point-to-down-hover.svg");
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
            }
        }
        //Галочка слева, при выбранной позиции
        &:before {
            content: "";
            display: none;
            width: 10px;
            height: 8px;
            background-image: url("~images/active_custom_card/checked.svg");
            position: absolute;
            top: 5px;
            left: -15px;
        }
        //Треугольник справа
        &:after {
            content: "";
            position: absolute;
            display: flex;
            width: 8px;
            height: 8px;
            top: 6px;
            right: -10px;
            background: url("~images/active_custom_card/arrow-point-to-down.png");
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            opacity: 0.6;               
            cursor: pointer;
        }
        // Название элемента сортировки 
    }
    &__name {
        display: flex;
        white-space: nowrap;
        margin-right: 5px;
    }
    //Выбранная позиция(span)
    &__selected-position {
        display: none;
        border-bottom: 1px solid $color-primary;
    } 
}
